nav {
    position: absolute;
    top: 0;
    width: 100%;
    height: 50px;
    background: black;
}

.list {
    list-style-type: none;
    background: black;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    font-size: 22px;
}

.item {
    margin: 0 15px;
    text-transform: uppercase;
    color: white;
    cursor: pointer;
}

.btn {
    display: none;
    position: absolute;
    right: 10px;
    top: 7px;
    padding: 5px;
    /* color: white; */

    font-size: 18px;
}

@media screen and (max-width: 500px) {
    .list {
        flex-direction: column;
        height: 200px;
    }

    .item:nth-child(1) {
        border-top: 1px solid rgba(255, 255, 255, 0.6);
        margin-top: 50px
    }

    .item {
        height: 70px;
        width: 100%;
        border-bottom: 1px solid #ffff;
        line-height: 50px;
        text-align: center;
    }

    .btn {
        display: block;
    }

}